<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

    .num-input {
        width: 100px;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">
                <el-col class="filter-label" :span="2">规则编号:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.id" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2">状态:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="filter.status" placeholder="请选择">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col class="filter-label" :span="2">权限名称:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.name" placeholder="请输入"></el-input>
                </el-col>

                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
                    <!--<div style="margin-left: 30px;color: dodgerblue;display: inline-block" @click="filterOpen">-->
                    <!--{{filter.isOpen?"关闭":'展开' }}-->
                    <!--</div>-->
                </el-col>
            </el-row>
            <el-row class="filter">

                <el-col class="filter-label" :span="2">权限等级:</el-col>
                <el-col :span="4">
                    <el-select class="filter-match" v-model="filter.level" placeholder="请选择">
                        <el-option
                                label="一级"
                                :value="1">
                        </el-option>
                        <el-option
                                label="二级"
                                :value="2">
                        </el-option>
                        <el-option
                                label="三级"
                                :value="3">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col class="filter-label" :span="2">排序:</el-col>
                <el-col :span="4">
                    <el-input class="filter-input" v-model="filter.sort" placeholder="请输入"></el-input>
                </el-col>

                <el-col class="filter-label" :span="2">权限描述:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.description" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2">创建时间:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
            </el-row>
        </div>
        <el-table
                :data="data.data"
                stripe
                style="width: 100%;margin-top: 20px">
            <el-table-column
                    prop="id"
                    label="id">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="名称">
            </el-table-column>
            <el-table-column
                    prop="level"
                    label="等级">
            </el-table-column>
            <el-table-column
                    prop="path"
                    label="路由">
            </el-table-column>
            <el-table-column
                    prop="sort"
                    label="排序">
            </el-table-column>
            <el-table-column
                    prop="createtime"
                    label="操作时间">
            </el-table-column>
            <el-table-column
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'启用':'未启用'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="detail(scope.row)" type="text" size="small">详细</el-button>
                    <el-button @click="del(scope)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="data.last_page"
                    @current-change="loadData">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import api from '../../js/api/permission'

    export default {
        name: "User",
        data() {
            return {
                options: [
                    {
                        value: 1,
                        label: '正常'
                    },
                    {
                        value: 0,
                        label: '禁用'
                    }
                ],
                filter: {
                    id: null,
                    name: null,
                    sort: null,
                    level: null,
                    time: null,
                    status: null,
                    create_start: null,
                    create_end: null,
                    description: null,
                    jump_page: null,
                    isOpen: false,
                },
                data: {}
            }
        },
        mounted() {
            this.loadData(1);
        },
        methods: {
            loadData(page) {
                this.filter.jump_page = page;
                if (this.filter.time != null && this.filter.time.length === 2) {
                    this.filter.create_start = this.filter.time[0];
                    this.filter.create_end = this.filter.time[1]
                } else {
                    this.filter.create_start = null;
                    this.filter.create_end = null
                }
                api.nodeList(this.filter, res => {
                    this.data = res.data.data;
                })

            },
            filterOpen() {
                this.filter.isOpen = !this.filter.isOpen;
            },
            add() {
                this.$router.push({
                    name: "permissionDetail",
                });
            },
            ref() {
                this.filter = {
                    id: null,
                    name: null,
                    sort: null,
                    level: null,
                    status: null,
                    time: null,
                    create_start: null,
                    create_end: null,
                    description: null,
                    jump_page: null,
                    isOpen: false,
                };
                this.loadData(1);

            },
            del(scope) {
                let _this = this;
                this.$confirm('确认删除' + scope.row.name + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })
                })
            },
            detail(item) {
                this.$router.push({
                    name: "permissionDetail",
                    query: {
                        id: item.id,
                    },
                });
            },
        }
    }
</script>

